* {
  box-sizing: border-box;
}

body, h1, h2, h3, p, ul {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

html {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 400;
}

.header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo-link {
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.header__logo-image {
  width: 100%;
  height: 100%;
}

.header__nav {
  margin-right: 50px;
}

.header__list {
  display: flex;
}

.header__item:not(:first-child) {
  margin-left: 20px;
}

.header__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  transition: color .3s;
  display: inline-block;
}

.header__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.catalog {
  grid-template-columns: repeat(var(--grid-column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --grid-column-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --grid-column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --grid-column-count: 4;
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  object-fit: contain;
  width: 100%;
  height: 134px;
  display: block;
}

.card__title {
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__article {
  color: #616070;
  width: 100%;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 16px;
  display: flex;
}

.card__reviews-count {
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 24px;
  display: flex;
}

.card__price-title {
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-link {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  transition-property: background-color, color;
  transition-duration: .3s;
}

.card__buy-link:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars--0 :nth-child(-n), .stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}
